.btn-flotante {
  width: 70px;
	position: fixed;
	bottom: 40px;
	left: 40px;
	transition: all 300ms ease 0ms;
	z-index: 99;
}
.btn-flotante:hover {
	transform: translateY(-7px);
}
@media (max-width: 767.98px) {
 	.btn-flotante {
		width: 30%;
		font-size: 14px;
		padding: 12px 20px;
		bottom: 15px;
		left: 3px;
	}
} 